import React, { FC } from "react";
export interface IOrderDefaultInfoProps {
  numberOrder: string;
  promocode: string;
  totalPrice: string;
}

const OrderDefaultInfo: FC<IOrderDefaultInfoProps> = ({
  numberOrder,
  promocode,
  totalPrice,
}) => {
  return (
    <div>
      <div className="orderItem__id"> : {numberOrder}</div>
      <div className="orderItem__promocode">: {promocode}</div>
      <div className="orderItem__promocode">
         : {`${totalPrice} `}
      </div>
    </div>
  );
};

export default OrderDefaultInfo;
